import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { backendUrl } from '../../config/config';
import logo from '../../assets/components/Wrapper/Header/logo.webp';

import './index.scss';
import { Helmet } from 'react-helmet';
import ReactStars from 'react-rating-stars-component';
import HeadingBlock from '../../components/HeadingBlock';
import Comments from './CommentComponent/Comments/Comments';
import Spinner from "../../components/Spinner";
import ErrorIndicator from "../../components/Errors/ErrorIndicator";
import dayjs from 'dayjs';

const BlogArticle = React.memo((props) => {

    const [ loading, setloading ] = useState(false);
    const [ error, setError ] = useState(false);
    const [ post, setPost ] = useState(null);
    const [ postId, setPostId ] = useState(null);
    const [ showComm, setShowComm ] = useState(false);

    useEffect(() => {
        setloading(true);

        axios.post(backendUrl + '/posts/get/byroute', {
            route: props.match.params.route
        })
            .then(res => {
                setError(false);
                setloading(false);
                setPost(res.data.posts);
                setPostId(res.data.posts._id);
            })
            .catch(err => {
                setError(true);
                setloading(false);
            })
    }, [props]);

    let moderatedComments = [];
    post ? moderatedComments = post.comments.filter(el => el.status !== 'New') 
         : moderatedComments = []; 

    const createJSONLD = () => {
        if (post) {
            const date = {
                '@context': 'https://schema.org/',
                "@type": "CreativeWork",
                "author": {
                    "@type": 'Organization',
                    'name': '24ROLLS',
                },
                'contentLocation': 'Днепр, Запорожье',
                'about': post.title,
                'text': post.content,
                'dateCreated': dayjs(post.date).format('DD.MM.YYYY'),
                'datePublished': dayjs(post.date).format('DD.MM.YYYY'),
                'keywords': 'Суши, роллы, Харьков, Днепр, Днепропетровск, Запорожье, вкусно, быстрая доставка',
                'material': 'Блог',
                'contentRating': post.rate,
                'comment': moderatedComments.map(el => el.content).join(', '),
                'commentCount': moderatedComments.length,
                'description': post.content.slice(0, 300)
            };
    
            return JSON.stringify(date);
        }
    };

    const onChangeCommMode = () => {
        setShowComm(!showComm);
    }

    return (
        <div className={'blog-article_section'}>
            {loading && <Spinner /> }
            {error && <ErrorIndicator />}
            {post &&
                <>  
                <Helmet>
                    <title>{post.seo_title}</title>
                    <meta name="description" content={post.seo_description} />
                    <meta name="keywords" content={post.seo_keywords} />
                    <script async type="application/ld+json">
                        {createJSONLD()}
                    </script>
                    <script type="application/ld+json">
                        {JSON.stringify({ "@context": "http://schema.org", "@type": "WebSite", "@id": "#website", "url": "https://24rolls.zp.ua/", "name": "\u0421\u0443\u0448\u0438 24Rolls Dnepr", "potentialAction": { "@type": "SearchAction", "target": "https://24rolls.zp.ua/?s={search_term_string}", "query-input": "required name=search_term_string" } })}
                    </script>
                    <script type="application/ld+json">
                        {JSON.stringify({ "@context": "http://schema.org", "@type": "Organization", "url": "https://24rolls.zp.ua/", "logo": logo, "contactPoint": [{ "@type": "ContactPoint", "telephone": "068 719 24 24", "contactType": ["customer service", "sales"], "areaServed": ["UA"], "contactOption": ["TollFree"], "availableLanguage": ["Russian", "Ukrainian"] }], "sameAs": ["https://www.youtube.com/channel/UCmJwKEidDraJtVdYtpnsJSA", "https://www.instagram.com/24rolls.com.ua/"] })}
                    </script>
                </Helmet>
                    <HeadingBlock title={'Блог'}/>
                    <div className={'blog_container'}>
                        <div className={'imageTitle'}>
                            <img
                                src={backendUrl + `/${post.image}`}
                                alt={post.alt}
                            >
                            </img>
                        </div>
                        <div className={'blog_container_filling'}>
                            <div className={'blog_container_filling_header'}>
                                <h1>
                                    {post.title}
                                </h1>
                                <p>
                                    {dayjs(post.date).format('DD.MM.YYYY')}
                                </p>
                            </div>
                            <div className={'blog_container_filling_center'}>
                        <span>
                            {post.content}
                        </span>
                            </div>
                            <div className={'blog_container_filling_bottom'}>
                                <div className="blog_container_filling_bottom__rate">
                                    <ReactStars
                                    count={5}
                                    value={post.rate}
                                    edit={false}
                                    size={20} 
                                    isHalf={true}
                                    />
                                    <span className="blog_container_filling_bottom__rate__label">
                                        ({post.rate.toFixed(1)})
                                    </span>
                                </div>
                                <p onClick={onChangeCommMode}>
                                    {'Комментарии '}{`(${moderatedComments.length})`}
                                </p>
                            </div>
                        </div>
                        <Comments 
                            {...props}
                            comments={moderatedComments}
                            postId={postId}
                            opened={showComm} 
                        />
                    </div>
                </>
            }
        </div>
    )
});

export default BlogArticle;