import React, { useState } from 'react';
import axios from 'axios';
import { backendUrl } from '../../../../config/config';
import { checkValidaty } from '../../../../tools/checkValidaty';

import './writeComment.scss';
import Spinner from '../../../../components/Spinner';
import Modal from '../../../../components/UI/Modal/Modal';

const WriteComment = (props) => {

    const [ content, setContent ] = useState({
        value: '',
        valid: false,
        validation: {
            required: true,
            maxLength: 300
        }
    });
    const [ loading, setLoading ] = useState(false);
    const [ error, setError ] = useState(false);
    const [ modalPost, setModalPost ] = useState(false);

    const onButtonPressedHandler = () => {
        setLoading(true);

        axios.post(backendUrl + '/comment/', {
            content: content.value,
            postId: props.postId
        }, {
            headers: {
                'Authorization': `Bearer ${props.token}`
            }
        })
        .then(res => {
            setError(false);
            setModalPost(true);
            setContent({
                value: '',
                valid: false,
                validation: {
                    required: true,
                    maxLength: 300
                }
            });
        })
        .catch(err => {
            setError(true);
        })
        .finally(() => setLoading(false));
    }

    return (
        <div className="blog-yours-comment">
            <Modal
                show={modalPost}
                modalClosed={() => setModalPost(false)}>
                <div style={{textAlign:'center'}}>
                    <p>{'Ваш комментарий в скором времени опубликуют'}</p>
                </div>
            </Modal>
            <textarea
                value={content.value}
                onChange={(e) => {
                    setContent({
                        ...content,
                        value: e.target.value,
                        valid: checkValidaty(e.target.value, content.validation)
                    })
                }}
                placeholder="Оставьте комментарий"
            />
            {loading && !error && <Spinner />}
            {!loading && !error &&
            <div className="blog-yours-comment-buttons">
                <button className="blog-yours-comment-cancel-button">
                    {'Отменить'}
                </button>
                <button
                    onClick={onButtonPressedHandler}
                    className={
                        content.valid ? "blog-yours-comment-submit_button"
                                    : "blog-yours-comment-cancel-button"
                        }
                    style={{
                        marginLeft: '10px'
                    }}
                >
                    {'Оставить комментарий'}
                </button>
            </div>
            }
            {error && <p className="blog-yours-comment__error-field">{'Произошла серверная ошибка...'}</p>}
        </div>
    );
};

export default WriteComment;