import React from 'react';
import store from 'store';
import { Link } from 'react-router-dom';
import SingleComment from '../SingleComment/SingleComment';
import WriteComment from '../WriteComment/WriteComment';

import './comments.scss';

const Comments = React.memo((props) => {

    const { user } = store.get('24rolls');
    const token = user ? user.token : null;

    return (
        <div className={
            props.opened ? "blog-comments-container blog-comments-container-opened" :
                           "blog-comments-container blog-comments-container-closed"
        }>
            {
                token ?
                <WriteComment 
                    token={token}
                    postId={props.postId} 
                /> :
                <p className="blog-comments-container__yours-comment-blocked" >{'Чтобы оставлять комментарии вы должны '} 
                    <Link to={props.match.path.slice(0, 3) + '/log-in'}>
                        {'Войти'}
                    </Link>
                </p>
            }
            {
                props.comments.map((el, index) => 
                    <SingleComment 
                        key={index}
                        data={el.date} 
                        content={el.content}
                        name={el.userId.name}
                    /> 
                )
            }
        </div>
    );
});

export default Comments